/**
 * Hook para detectar breakpoints responsivos
 * 
 * Centraliza a lgica de deteco de tamanhos de tela,
 * eliminando duplicao de cdigo de verificao mobile/tablet/desktop.
 * 
 * @returns {Object} Objeto com informaes do breakpoint atual
 * @property {string} breakpoint - Nome do breakpoint ('mobile' | 'tablet' | 'desktop')
 * @property {boolean} isMobile - Se  mobile
 * @property {boolean} isTablet - Se  tablet
 * @property {boolean} isDesktop - Se  desktop
 * @property {number} width - Largura atual da janela
 * 
 * @example
 * const { breakpoint, isMobile } = useBreakpoint();
 * 
 * return (
 *   <div>
 *     {isMobile ? <MobileMenu /> : <DesktopMenu />}
 *   </div>
 * );
 */
import { useState, useEffect } from 'react';

// Breakpoints consistentes em todo o projeto
const BREAKPOINTS = {
  mobile: 768,
  tablet: 1024,
};

export const useBreakpoint = () => {
  const [breakpoint, setBreakpoint] = useState('desktop');
  const [width, setWidth] = useState(typeof window !== 'undefined' ? window.innerWidth : 1024);

  useEffect(() => {
    const updateBreakpoint = () => {
      const currentWidth = window.innerWidth;
      setWidth(currentWidth);

      if (currentWidth <= BREAKPOINTS.mobile) {
        setBreakpoint('mobile');
      } else if (currentWidth <= BREAKPOINTS.tablet) {
        setBreakpoint('tablet');
      } else {
        setBreakpoint('desktop');
      }
    };

    // Verificar no mount
    updateBreakpoint();

    // Adicionar listener
    window.addEventListener('resize', updateBreakpoint);
    
    // Cleanup
    return () => {
      window.removeEventListener('resize', updateBreakpoint);
    };
  }, []);

  return {
    breakpoint,
    isMobile: breakpoint === 'mobile',
    isTablet: breakpoint === 'tablet',
    isDesktop: breakpoint === 'desktop',
    width,
  };
};

export default useBreakpoint;


